import DialogItem from './DialogItem/DialogItem'
import MsgItem from './Message/Message'
import cl from './Dialogs.module.css'
import { Field, reduxForm } from 'redux-form'
import { Textarea } from '../common/FormsControls/FormsControls';
import { maxLength, requiredField } from '../../utils/validators/validators';

const Dialogs = (props) => {
    let addNewMessage = (values) => {props.sendMessage(values.newMessageBody)}
    return (
        <div className={cl.dialogs}>
            <div className={cl.dialogs_items}>
                <ul className={cl.names}>
                    {props.state.dialogData.map(el => (<DialogItem name={el.name} key={el.id} id={el.id} />))}
                </ul>
            </div>
            <div className={cl.messages}>
                <div className=""> {props.state.messagesData.map(el => (<MsgItem msg={el.message} key={el.id} />))}</div>
                <div className="Form">
                    <AddMessageFormRedux onSubmit={addNewMessage}/>
                </div>
            </div>
        </div>
    )
}
let maxLength50 = maxLength(50)
const AddMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className="">
            <div className="">
                <Field component={Textarea} name='newMessageBody' placeholder='Enter your Message'
                validate={[requiredField,maxLength50 ]}/>
            </div>
            <div className=""><button>Send</button></div>
        </form>
    )
}
const AddMessageFormRedux = reduxForm({form:'diallogAddMessageForm'})(AddMessageForm)

export default Dialogs
